import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
	selector: 'app-register',
	templateUrl: './register.component.html',
	styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {

	static TAG_DATE_FORMAT_VALUE_REGX: string = '^(0[1-9]|1[0-2])\/(0[1-9]|[1-2][0-9]|3[0-1])\/[0-9]{4}$';
	static TAG_TIME_FORMAT_VALUE_REGX: string = '^(0[0-9]|1[0-2]):[0-5][0-9] *(A|a|P|p)(M|m)$';
	registerForm: FormGroup;
	visitReasonFormGroup: FormGroup;
	pickupFormGroup: FormGroup;
	parentmeetingFormGroup: FormGroup;
	otherFormGroup: FormGroup;
	
	qrCode: boolean = false;
	registrationStr: string = '';
	
	constructor(private fb: FormBuilder) {
		this.registerForm = this.fb.group({
			firstname: ['', [Validators.required]],
			middlename: [''],
			lastname: ['', [Validators.required]],
			phone: ['', [Validators.required, Validators.pattern('[- +()0-9]{6,}')]],
			email: ['', [Validators.required, Validators.email]],
			visittype: ['', [Validators.required]],
			visitdate: ['', [Validators.required, Validators.pattern(RegisterComponent.TAG_DATE_FORMAT_VALUE_REGX)]],
			visittime: ['', [Validators.required, Validators.pattern(RegisterComponent.TAG_TIME_FORMAT_VALUE_REGX)]],
			
		});
		
		this.visitReasonFormGroup = this.fb.group({
            visitreason: ['', [Validators.required, Validators.compose([Validators.minLength(1), Validators.maxLength(250)])]],
        });

		this.pickupFormGroup = this.fb.group({
            studentname: ['', [Validators.required]],
			studentid: [''],
			grade: ['', [Validators.required]],
        });

		this.parentmeetingFormGroup = this.fb.group({
            teachername: ['', [Validators.required]]
        });

		this.otherFormGroup = this.fb.group({
            vendorname: ['', [Validators.required]],
        });
	}

	ngOnInit(): void {
	}

	register() {
		this.qrCode = true;
		const registration = Object.assign({}, this.registerForm.value, this.visitReasonFormGroup.value, this.pickupFormGroup.value, this.parentmeetingFormGroup.value, this.otherFormGroup.value);
		this.registrationStr = JSON.stringify(registration);
		//console.log(registration);
		console.log(this.registrationStr);
	}
}
